import react,{useState,useEffect} from 'react'
import "./css/style.css";
const Tempapp=()=>{
      
     const [city,setCity]=useState([]);
     const [search,setSearch]=useState("Lucknow");

     useEffect(()=>{
         const fetchapi=async()=>{
             const url=`http://api.weatherstack.com/current?access_key=c2ab7b411ce3d686e0298c1d2439640e&query=${search}`
             const response= await fetch(url);
            
             const resjson=await response.json();
             console.log(resjson)
             setCity(resjson.current);
         }
     fetchapi()
     },[search])

    return(
        <>
        <div className="box">
        <div className="inputData">
            <input type="search"
            value={search}
             onChange={(event)=>{setSearch(event.target.value)}} 
             className="inputField"/>
        </div>
       {!city ?(
           <p>No Data found</p>
       ) : (
        <div className="info">
        <h2 className="location">
        
        <i class="fas fa-cloud-sun-rain"><br/>{search}</i>
        </h2>
        <h1 className="temp">Temperature:{city.temperature}Â°C</h1> 
        <h1 className="temp"></h1>    
        <h3 className="tempmin_max">humidity:{city.humidity} |Wind-speed:{city.wind_speed}</h3>
      </div>
      )}
      
        </div>
        </>
    )
}
export default Tempapp;